import React, { useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Link, useLocation } from "react-router-dom";
import { CSidebar, CSidebarNav, CSidebarNavDivider } from "@coreui/react";
import Img from "../assets/icons/logistical_square.png";
import WmsMenu from "../assets/icons/wmsMenu.svg";
import WmsMenuGreen from "../assets/icons/wmsMenuGreen.svg";
import { roleChecking, setDropdown } from "./services";

const TheSidebar = () => {
  const dispatch = useDispatch();
  const location = useLocation();

  const show = useSelector((state) => state.sidebarShow);
  const expandSidebar = true;//useSelector((state) => state.expandSidebar);
  const _nav = useSelector((state) => state._nav);
  const userRole = useSelector((state) => state?.user?.role);
  const userModules = useSelector((state) => state?.user?.modules);

  const [hover, setHover] = useState(null);

  const expandSidebarHandler = (props) => {
    dispatch({
      type: "EXPAND_SIDEBAR",
      expandSidebar: expandSidebar,
    });
  };

  return (
    <CSidebar
      style={{ width: `${expandSidebar ? "" : "4rem"}` }}
      show={show}
      unfoldable
      onShowChange={(val) => dispatch({ type: "set", sidebarShow: val })}
    >
      {/* <CSidebarBrand className=" flex justify-start ml-0" to="/">
        
      </CSidebarBrand> */}
      <div
        className={`w-100 d-flex justify-content-between mt-3 mb-4 pl-3 pr-3 align-items-center`}
      >
        <div className="w-100 d-flex justify-content-start align-items-center">
          <img
            src={Img}
            style={{ maxWidth: "30px" }}
            className="mr-2 cursor-pointer "
            onClick={() => expandSidebarHandler(true)}
          />
          <span
            className={`LatoRegular m-0 h5 ${expandSidebar ? "" : "d-none"}`}
          >
            Logistical
          </span>
        </div>
        <span
          className={`iconU-sideBarHideLeft ${expandSidebar ? "" : "d-none"}`}
          onClick={() => expandSidebarHandler(false)}
        />
      </div>

      <div className="d-flex flex-column h-100 w-100">
        <CSidebarNav>
          {_nav
            ?.filter((menu) => roleChecking(userRole, menu))
            ?.map((n, i) => {
              let string = location.pathname;
              const isActive = string.includes(n.to);
              const isOpen = n.isOpen;
              const isHover = hover === n.to;
              const noDropdown = "CSidebarNavItem";

              return (
                <div
                  key={i}
                  className="c-sidebar-item links ml-4"
                  onMouseEnter={() => setHover(n.to)}
                  onMouseLeave={() => setHover(null)}
                  onClick={() => expandSidebarHandler(true)}
                >
                  <Link
                    key={i}
                    to={n.to === "/user-management" ? n.to : "#"}
                    className={`${isActive} d-flex align-items-center justify-content-between mb-4`}
                    onClick={(e) =>
                      setDropdown({ name: n.name, i, dispatch, isOpen, _nav })
                    }
                  >
                    <div className="d-flex align-items-center">
                      <span
                        className={`${
                          n.icon
                        } m-0 d-flex align-items-center mr-3 text-center ${
                          isHover || isActive ? "green" : "text-white"
                        }`}
                      />
                      <div
                        className={`text-nowrap ${
                          isHover || isActive ? "green" : "text-white"
                        } ${expandSidebar ? "" : "d-none"}`}
                      >
                        {n.name}
                      </div>
                    </div>
                    <span
                      className={`${
                        noDropdown === n._tag
                          ? ""
                          : null
                      } ${
                        isOpen
                          ? ""
                          : "text-white "
                      } m-0 d-flex align-items-center mr-3 text-center ${
                        isHover || isActive
                          ? "green "
                          : "text-white "
                      } ${expandSidebar ? "" : "d-none"}`}
                    />
                  </Link>
                  {n._children?.map((c, h) => {
                    const isChildActive = string.includes(c.to);
                    const isChildHover = hover === c.to;
                    const moduleAccess = userModules?.includes(c.moduleName)
                      ? null
                      : "d-none";
                    // const moduleAccess = null
                    return (
                      <div
                        key={h}
                        className={`c-sidebar-item links dropdown-transition ${
                          !isOpen ? "collapse-dropdown" : null
                        }`}
                        onMouseEnter={() => setHover(c.to)}
                        onMouseLeave={() => setHover(null)}
                        onClick={() => expandSidebarHandler(true)}
                      >
                        <Link
                          key={h}
                          to={c.to}
                          className={`${isChildActive} ${moduleAccess} d-flex align-items-center mb-4`}
                        >
                          <span
                            className={`${
                              c.icon
                            } m-0 d-flex align-items-center mr-3 ml-1 ${
                              isChildHover || isChildActive
                                ? "green"
                                : "text-white"
                            }`}
                          />
                          <div
                            className={`text-nowrap ${
                              isChildHover || isChildActive
                                ? "green"
                                : "text-white"
                            } ${expandSidebar ? "" : "d-none"}`}
                          >
                            {c.name}
                          </div>
                        </Link>
                      </div>
                    );
                  })}
                </div>
              );
            })}
          <CSidebarNavDivider />
        </CSidebarNav>
        <CSidebarNav className="d-flex flex-column justify-content-end p-3">
          <div
            onClick={() =>
              window.location.replace(window.config.REACT_APP_LOGIN)
            }
            onMouseEnter={() => setHover(true)}
            onMouseLeave={() => setHover(!hover)}
            style={{ backgroundColor: "#455061" }}
            className={`w-100 d-flex justify-content-center align-items-center rounded cursor-pointer py-2 ${
              expandSidebar ? "px-5" : "px-3"
            }`}
          >
            <img
              className="cursor-pointer"
              src={hover === true ? WmsMenuGreen : WmsMenu}
            />
            <label
              className={`m-0 ml-3 cursor-pointer ${
                hover === true ? "green" : "text-white"
              } ${expandSidebar ? null : "d-none"}`}
            >
              WMS Menu
            </label>
          </div>
        </CSidebarNav>
      </div>
    </CSidebar>
  );
};

export default React.memo(TheSidebar);
